    .featuretext h1 {
        font-size: 45px;
    }
    .featuretext p {
        font-size: 18px;
        color: gray;
    }
    .userdes h1 {
        font-size: 45px;
        padding-bottom: 5%;
        font-family: 'Poppins';
    }
    .userdes p {
        font-size: 18px;
        font-family: 'Poppins';
    }
    .userappdescription img {
        height: 75vh;
    }
    .highlightElement {
        width: 48%;
